@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
.CousreCardTop{
    width: 280px;
    border: 2px solid #309255;
    border-radius: 5px;
    padding: 15px;
    margin-bottom: 28px;
}

.CousreCardTop .CousreCardHeader{ 
    display: flex;
    justify-content: space-between;
}
.CousreCardTop .CousreCardHeader .left,
.CousreCardTop .CousreCardHeader .right{
    width: 50%;
    
    display: flex;
    align-items: center;
}
.CousreCardTop .CousreCardHeader .left img{
    width: 100%;
    border-radius: 5px;
}
.CousreCardTop .CousreCardHeader .right {
    justify-content: end;
}
.CousreCardTop .CousreCardHeader .right div{
    text-align: end;
}
.CousreCardTop .CousreCardHeader .right h1{
    font-family: 'Open Sans', sans-serif;
    font-size: 17px;
    font-weight: bold;
    color: #309255;
}
.CousreCardTop .courseInstructor{
   display: flex;
   align-items: center;
   margin-top: 15px;
}

.CousreCardTop .courseInstructor img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 10px;
}
.CousreCardTop .courseInstructor h1{
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    color: #959494;
}
.CousreCardTop h3{
    font-size: 17px;
    font-family: 'Open Sans', sans-serif;
    margin-top: 10px;
    color: #309255;
}